.img-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: auto;
    & div{
        display: flex;
        background-color: white;
        margin: 1em;
        padding: 0.5em;
        border-radius: 20px;
        box-shadow: 0px 0px 33px -3px rgba(0,0,0,0.75);
    }& div:hover{
        transform: translateY(-3px);
        transition: all 0.5s;
    }& img{
        width: 4.5em;
        object-fit:contain;
    }

}


